import { h, Component } from 'preact';
import { observer } from 'mobx-preact';
import { Text, Localizer } from 'preact-i18n';
import util from 'preact-util';

const initialState = {
    menu: {
		profile: false,
		main: false,
	},
	searchText: '',
};

@observer
class Navigation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
		this.apiServer = util.getApiServer();
    }

    render() {
		const { appState, userStore, workoutStore } = this.props.stores;
		const { currentTeam } = workoutStore;
		const { user = {} } = userStore;
		const { path } = appState;

		let pathFixed = path.replace(/^\//, '');
		pathFixed = pathFixed.replace(/\/$/, '');
		pathFixed = pathFixed.replace(/\d+/g, 'id');
		pathFixed = pathFixed.replace(/\/$/, '');
		pathFixed = pathFixed.replace(/\//g, '.');
		if (pathFixed === '') {
			pathFixed = 'frontpage';
		}

		return (
			<div class='navbar-themed fixed-bottom'>
				<div class='container-fluid'>
					<div class='row'>
						<div class={`col-3 text-center p-2 ${path.match(/\/dogs/) ? 'border-bottom border-primary' : ''}`}>
							<a href='/dogs/' class={`text-${path.match(/\/dogs/) ? 'primary' : 'muted'}`}>
								<i class='fas fa-dog' style='font-size: 25px;' /><br />
								<Text id='footer.dogs'>Hundene</Text>
							</a>
						</div>
						<div class={`col-3 text-center p-2 ${path.match(/\/workouts/) ? 'border-bottom border-primary' : ''}`}>
							<a href='/workouts/' class={`text-${path.match(/\/workouts/) ? 'primary' : 'muted'}`}>
								<i class='fas fa-running' style='font-size: 25px;' /><br />
								<Text id='footer.workouts'>Treningen</Text>
							</a>
						</div>
						<div class={`col-3 text-center p-2 ${path.match(/\/teams/) ? 'border-bottom border-primary' : ''}`}>
							<a href='/teams/' class={`text-${path.match(/\/teams/) ? 'primary' : 'muted'}`}>
								<i class='fas fa-users' style='font-size: 25px;' /><br />
								<Text id='footer.teams'>Teamene</Text>
							</a>
						</div>
						<div class={`col-3 text-center p-2 ${path.match(/\/tracks/) ? 'border-bottom border-primary' : ''}`}>
							<a href='/tracks/' class={`text-${path.match(/\/tracks/) ? 'primary' : 'muted'}`}>
								<i class='fas fa-map-marked-alt' style='font-size: 25px;' /><br />
								<Text id='footer.tracks'>Rundene</Text>
							</a>
						
							{user && user.id && path && <img src={`${this.apiServer}/api/pageview/${pathFixed}/${user.id}/${currentTeam}/${new Date().getTime()}`} style='width: 1; height: 1; opacity: 0.1;' />}
						</div>
					</div>
				</div>
			</div>
		);
	}
}

export default Navigation;
