import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { Text, Localizer, withText } from 'preact-i18n';

import mu from '../../lib/musher-util';

@observer
class Competition extends Component {
    async loadAll() {
		this.setState({ isLoading: true });
		await this.loadResults();
		this.setState({ isLoading: false });
    }

	loadResults = async () => {
		const { workoutStore } = this.props.stores;
        await workoutStore.workoutResults();
	}

    componentDidMount() {
        this.loadAll();
    }

    render() {
        const { workoutStore, userStore } = this.props.stores;
        const { competitionResults, competitionAllResults, competitionTeams } = workoutStore;
        return (
            <div class='col-12'>
                <h5>Competition results</h5>
                <div>
                    <table class='table table-striped table-sm'>
                        <tbody>
                            {competitionResults.map(res => <>
                            <tr>
                                <th>Date</th>
                                <th>Type</th>
                                <th>Ambitions</th>
                                <th>Count</th>
                                <th>Avg dogs</th>
                                <th>total km</th>
                                <th>km</th>
                                <th>elevation</th>
                                <th>duration</th>
                                <th>speedAvg</th>
                            </tr>
                                <tr>
                                    <td class='font-weight-bold'>{res.year}-{res.month}</td>
                                    <td class='font-weight-bold'>{userStore.findTeamType(res.type)} ({res.type})</td>
                                    <td class='font-weight-bold'>{userStore.findTeamAmbition(res.ambitions)} ({res.ambitions})</td>
                                    <td class='text-right font-weight-bold'>{util.format(res.count, 1)}</td>
                                    <td class='text-right font-weight-bold'>{util.format(res.avgDogs, 0)}</td>
                                    <td class='text-right font-weight-bold'>{util.format(res.totalDistanceKm, 1)} km</td>
                                    <td class='text-right font-weight-bold'>{util.format(res.distanceKm, 1)} km</td>
                                    <td class='text-right font-weight-bold'>{util.format(res.elevation, 0)} m</td>
                                    <td class='text-right font-weight-bold'>{util.secToHms(res.duration)}</td>
                                    <td class='text-right font-weight-bold'>{util.format(res.speedAvg, 1)} km/t</td>
                                </tr>
                                <tr>
                                    <td colspan='10' class='mb-5 pb-5 pt-2'>
                                        <small>
                                        <table class='table table-striped table-sm'>
                                            <thead>
                                                <tr>
                                                    <th>Team</th>
                                                    <th>Email</th>
                                                    <th>Place</th>
                                                    <th>Country</th>
                                                    <th>Count</th>
                                                    <th>Avg dogs</th>
                                                    <th>total km</th>
                                                    <th>km</th>
                                                    <th>elevation</th>
                                                    <th>duration</th>
                                                    <th>speedAvg</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                {/* {
                                                        "team":773,
                                                        "type":1,
                                                        "ambitions":1,
                                                        "year":2020,
                                                        "month":11,
                                                        "count":2,
                                                        "distanceKm":14.1,
                                                        "totalDistanceKm":28.2,
                                                        "diffTotalDistanceKm":18.22078,
                                                        "elevation":0,
                                                        "duration":3000,
                                                        "speedAvg":null,
                                                        "avgDogs":2
                                                    },
                                                */}
                                                {competitionAllResults.filter(t => t.type === res.type && t.ambitions === res.ambitions).map((line) => {
                                                    const team = competitionTeams.find(e => e.id === line.team);
                                                    return (
                                                        <tr>
                                                            <td>{line.team}: {team.name}</td>
                                                            <td>{team.email}</td>
                                                            <td>{team.postalcode} {team.place}</td>
                                                            <td>{team.country}</td>
                                                            <td class='text-right'>{util.format(line.count, 0)}</td>
                                                            <td class='text-right'>{util.format(line.avgDogs, 1)}</td>
                                                            <td class='text-right'>
                                                                {util.format(line.totalDistanceKm, 1)} km
                                                                <span class={`ml-2 text-${res.totalDistanceKm > line.totalDistanceKm ? 'danger' : 'success'}`}>
                                                                    {res.totalDistanceKm > line.totalDistanceKm ? '-' : '+'}{util.format(line.diffTotalDistanceKm, 1)}
                                                                </span>
                                                            </td>
                                                            <td class='text-right'>{util.format(line.distanceKm, 1)} km</td>
                                                            <td class='text-right'>{util.format(line.elevation, 0)} m</td>
                                                            <td class='text-right'>{util.secToHms(line.duration)}</td>
                                                            <td class='text-right'>{util.format(line.speedAvg, 1)} km/t</td>
                                                        </tr>
                                                    );
                                                })}
                                            </tbody>
                                        </table>
                                        </small>
                                    </td>
                                </tr>
                            </>)}
                        </tbody>
                    </table>
                </div>
            </div>
        );
    }
}

export default Competition;